function orderVO(){
	var o = {
		/**
		    * id
		    */
		   id:0,
		
		   /**
		    * 用户id
		    */
		    userId:'',
		
		   /**
		    * 桌位id
		    */
		    tableId:0,
		
		   /**
		    * 订单号
		    */
		    orderNum:'',
		
		   /**
		    * 价格
		    */
		    price:0,
		
		   /**
		    * 真实价格
		    */
		    realPrice:0,
		
		   /**
		    * 折扣价
		    */
		   discountPrice:0,
		
		   /**
		    * 支付方式 0现金 1微信 2支付宝
		    */
		   payType:0,
		
		   /**
		    * 状态 1：正常 2：已结账 3：异常
		    */
		    status:1,
		
		   /**
		    * 创建时间
		    */
		   createTime:'',
		
		   /**
		    * 修改时间
		    */
		    updateTime:'',
		
		   /**
		    * 创建人
		    */
		   createBy:'',
		
		    list:[]
	}
	return o
	
}

export {orderVO}