<template>
	<div>
		<el-row :gutter="20">
			<el-col :span="16">
				<div class="grid-content ">
					<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
						<el-menu-item :index="(index+'')" v-for="(item,index) in this.categorys" :key="index">
							<el-tag @click="toPath(item)" style="font-size: 20px;">{{item.name}}</el-tag>
						</el-menu-item>
					</el-menu>
				</div>
			</el-col>
			<el-col :span="8" >
				<div class="grid-content ">
					<el-tag style="font-size: 18px; margin-right:20px">{{this.orderVO.tableId}}号</el-tag>
					<el-tag style="font-size: 18px; margin-right: 20px;">总价：{{this.orderVO.price}}</el-tag>
					<el-button style="font-size: 18px; margin-left: 20px;" @click="sumbmin()">提交</el-button>
				</div>
			</el-col>
		</el-row>
		
		<el-table :data="foodTableData" style="width: 98%" >
			<el-table-column width="200" label="菜名" >
			  <template  slot-scope="scope">
				  <el-tag style="font-size: 20px;" >{{scope.row.name}}</el-tag>
			  </template>
			</el-table-column>
			<el-table-column width="100" label="状态">
				<template  slot-scope="scope">
					<el-tag size="medium" v-if="scope.row.status==1">正常</el-tag>
					<el-tag size="medium" v-if="scope.row.status==2">售完</el-tag>
					<el-tag size="medium" v-if="scope.row.status==3">禁用</el-tag>
				</template>
			</el-table-column>
			<el-table-column width="100" label="价格">
				<template  slot-scope="scope">
					<el-tag style="font-size: 20px;">{{scope.row.price}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column width="100" label="总价">
				<template slot-scope="scope">
					<el-tag style="font-size: 20px;">{{scope.row.allPreace}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column width="200" label="备注">
				<template  slot-scope="scope">
					<el-input type="textarea" v-model="scope.row.remark"></el-input>
				</template>
			</el-table-column>
			<el-table-column width="200" label="操作">
			  <template  slot-scope="scope">
				<el-input-number v-model="scope.row.sales"  @change="handleChange(scope.row)" :min="0" :max="100" label="描述文字"></el-input-number>
			  </template>
			</el-table-column>
		  </el-table>
	  </div>
</template>

<script>
	import {getAllCategory,saveFood,listFood,saveFoodOrderVO} from '../../api/index.js'
	// import food from '../../entity/food.js'
	import {orderVO} from '../../entity/orderVO.js'
	import {orderSku} from '../../entity/orderSku.js'
	import {dateUtil} from '../../util/dateUtil.js'
	
	export default{
		data(){
				return{
					recoderPrice:0,
					tableData:{},
					orderVO:{},
					activeIndex:'',
					allFoods:[],
					foodTableData:[
						{"id":7,
						"categoryId":5,
						"type":1,
						"name":"98",
						"sort":0,
						"price":8,
						"discountPrice":8,
						"sales":0,
						"remark":"",
						"status":1,
						"createTime":"2019-10-22 10:49:57",
						"updateTime":"2019-10-22 10:49:57",
						"createBy":"1"},
					],
					category:{},
					categorys:[]
				}
			},
		methods:{
			sumbmin(){
				this.orderVO.list=[]
				var j= 0
				for(var i=0;i<this.allFoods.length;i++){
					if(this.allFoods[i].sales>0){
						var orderSkua = orderSku()
						orderSkua.footId = this.allFoods[i].id
						orderSkua.footName = this.allFoods[i].name
						orderSkua.num = this.allFoods[i].sales
						orderSkua.remark = this.allFoods[i].remark
						orderSkua.price = this.allFoods[i].price
						if(this.orderVO.id !=0){
							orderSkua.status = 2
						}
						this.orderVO.list[j] = orderSkua
						j++
					}
				}
				saveFoodOrderVO(this.orderVO).then(r=>{
					this.$router.push({name:'Order'}) 
				})
			},
			toPath(category){
				this.category = category
				var j = 0
				this.foodTableData =[]
				for(var i=0; i<this.allFoods.length; i++){
					if(this.allFoods[i].categoryId == this.category.id){
						if(this.allFoods[i].allPreace ==null){
							this.allFoods[i].allPreace = 0;
						}
						this.foodTableData[j] =this.allFoods[i];
						j++
					}
				}
				
			},
			getAllFood(){
				listFood({}).then(r =>{
					this.allFoods = r.data
					this.getAllCategorys()
				})
			},
			getAllCategorys(){
				getAllCategory({}).then(r =>{
					if(r.data.code ==201){
						this.$router.push('/');
					}
					this.categorys = r.data;
					this.activeIndex = '0'
					this.toPath(this.categorys[0])
				})
			},
			handleSelect(key, keyPath){
				//console.log(key,"，，，"+keyPath)
			},
			handleChange(index) {
			    //console.log(index);
				index.allPreace = index.price*index.sales;
				this.counteAllPrice()
			},
			foodDel(index, row){
				//console.log(index,row);
			},
			handleDate(date){
				return date.substring(0,10);
			},
			counteAllPrice(){
				this.orderVO.price = this.recoderPrice;
				for(var i=0;i<this.allFoods.length;i++){
					if(this.allFoods[i].sales>0){
						this.orderVO.price+=(this.allFoods[i].sales*this.allFoods[i].price)
					}
				}
			}
		},
		created:function(){
			//请求类别数据
			this.getAllFood()
			if(this.$route.params){
				this.orderVO = this.$route.params.orderVO
				this.recoderPrice = this.orderVO.price;
			}
		}
	}
</script>

<style>
	.el-row {
	    margin-bottom: 20px;
	    &:last-child {
	      margin-bottom: 0;
	    }
	  }
	  .el-col {
	    border-radius: 4px;
	  }
	  .bg-purple-dark {
	      background: #99a9bf;
	    }
	    .bg-purple {
	      background: #d3dce6;
	    }
	    .bg-purple-light {
	      background: #e5e9f2;
	    }
	    .grid-content {
	      border-radius: 4px;
	      min-height: 36px;
	    }
	    .row-bg {
	      padding: 10px 0;
	      background-color: #f9fafc;
	    }
</style>
